.cards
{
	width: 100%;
	max-width: 70%;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	align-items: center;
	margin: 60px auto;
}

.cards h1
{
	position: relative;
	font-size: 40px;
	font-weight: bold;
	padding: 10px;
	margin: 20px 5px 5px;
}

.cards h1::before
{
	content: "";
	position: absolute;
	top: 0;
	width: 97%;
	height: 100%;
	border-bottom: 3px solid rgba(254, 81 ,149, 1);
}

.cards section
{
	padding: 10px;
	width: 90%;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	margin: 40px 0;
}

.cards section .card
{
	width: 40%;
	height: 230px;
	padding: 10px;
	display: flex;
	flex-direction: column;
	margin: 10px;
	flex-wrap: wrap;
	text-align: center;
	transition: .5s all ease;
	box-shadow: 0 2px 5px 0 black;
	border-radius: 0 0 5px 5px;
	overflow: hidden;
	position: relative;
}

.card::before
{
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 0;
	height: 100%;
	background: rgba(254, 132, 149, 1);
	transition: .7s all ease;
}

.card section 
{
	width: 150px;
	height: 150px;
	margin: 10px auto;
	position: relative;
	top: 20px;
	left: 95px;
	background-color: rgba(255, 255, 255, 1);
	border-radius: 5px;
	transition: .5s all ease;
}

.card section img
{
	width: 100%;
	height: 100%;
}

.card h2
{
	position: relative;
	top: 10px;
	left: -15px;
	padding: 10px;
	opacity: 0;
	border-radius: 5px;
	background-color: rgba(255, 255, 255, 1);
	transition: .9s ease;
	color: rgba(254, 81, 149, 1);
}

/*Hovers*/

.card:hover::before
{
	width: 100%;
	transition-delay: .20s;
}

.card:hover section
{
	left: -20px;
}

.card:hover h2
{
	top: 70px;
	opacity: 1;
	transition-delay: .5s;
}