.nav 
{
	padding: 15px;
	background: rgba(255, 255, 255, 1);
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	box-shadow: 0 2px 5px 0 black;
}

.nav section span
{
	padding: 5px;
	border-radius: 5px;
	font-weight: bold;
	font-size: 25px;
	background: rgba(254, 81 ,149, 1);
	color: rgba(255, 255, 255, 1);
}

.nav section a
{
	font-size: 18px;
	padding: 5px;
	margin: 5px;
	text-decoration: none;
	color: rgba(254, 81 ,149, 1);
	font-weight: bold;
	border-radius: 5px;
	transition: .5s all ease;
}

.nav section a:hover
{
	background-color:rgba(254, 81 ,149, 1);
	color: rgba(255, 255, 255, 1);
}