.footer
{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-evenly;
	align-items: center;
	position: absolute;
	width: 97.92%;
	padding: 7px 20px;
	top: 93.8vh;
	background-color: rgba(40, 40, 40, 1);
}

.footer a
{
	font-size: 40px;
	color: rgba(255, 255, 255, 1);
	transition: .5s all ease;
}

.footer a:hover
{
	color: rgba(254, 81 ,149, 1);
}